nav {
    background-color: rgb(17, 17, 17);
    color: white;
    padding: 1rem 1rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: sticky;
    top: 0;
    width: 100%;
    height: 5rem;
}

.logo {
    text-align: left;
    width: 60%;
    font-size: 1.2rem;
}

.menu-icon {
    display: flex;
    flex-direction: column;
    cursor: pointer;
}


i {
    background-color: rgb(255, 255, 255);
    width: 25px;
    height: 2px;
    margin: 2px;
    border-radius: 2px;
    z-index: 10;
    transition: all ease 0.5s; 
}

.open:nth-child(1){
    transform: rotate(45deg) translateY(9px);
}

.open:nth-child(2){
    opacity: 0;
}

.open:nth-child(3){
    transform: rotate(-45deg) translateY(-9px);
}

.close:nth-child(1){
    transform: rotate(0) translateY(0);
}

.close:nth-child(2){
    opacity: 1;
}

.close:nth-child(3){
    transform: rotate(0) translateY(0);
}

.links { 
    display: none;
}

.nav-links-closed{
    position: absolute;
    background-color: rgb(27, 27, 27);
    color: rgb(248, 248, 248);
    top: 4.9rem;
    /* transform: translateY(100vh); */
    width: 100%;
    height: 0vh;
    transition: all 0.2s ease;
    overflow: hidden;
}

.nav-links-closed ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.nav-links-closed ul li {
    margin: 3rem 0;
    font-size: 1.2rem;
    font-weight: 500;
    opacity: 0;
}

.nav-links {
    position: absolute;
    background-color: rgb(27, 27, 27);
    color: rgb(248, 248, 248);
    top: 4.9rem;
    width: 100%;
    height: 30.5rem;
    transition: all .5s ease;
}

.nav-links ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.nav-links ul li {
    margin: 3rem 0;
    font-size: 1.2rem;
    font-weight: 500;
    opacity: 1;
    transition: opacity .5s ease 0.1s;
}

@media screen and (min-width: 768px) {

    nav {
        padding: 1rem 3rem;
    }

    .logo {
        font-size: 1.3rem;
    }

    .menu-icon {
        display: none;
    }

    .nav-links-closed {
        display: none;
    }

    .nav-links {
        display: none;
    }

    .links {
        display: flex;
        width: 80%;
    }

    .links ul {
        width: 100%;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        
    }

    .links ul li {
        width: 80px;
        font-weight: 500;
    }
}

@media screen and (min-width: 1024px){

    nav {
        padding: 1rem 4rem;
    }

    .links ul {
        text-align: center;
    }
}
